import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HttpClientModule } from '@angular/common/http';
import { MyApp } from './app.component';
import { SignaturePadModule } from 'angular2-signaturepad';
import { HomePage } from '../pages/home/home';
import { Camera } from '@ionic-native/camera';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    SignaturePadModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/buscar-tienda/buscar-tienda.module.ngfactory#BuscarTiendaPageModuleNgFactory', name: 'BuscarTiendaPage', segment: 'buscar-tienda', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/eventos/eventos.module.ngfactory#EventosPageModuleNgFactory', name: 'EventosPage', segment: 'eventos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/firma-foto/firma-foto.module.ngfactory#FirmaFotoPageModuleNgFactory', name: 'FirmaFotoPage', segment: 'firma-foto', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/gerente/gerente.module.ngfactory#GerentePageModuleNgFactory', name: 'GerentePage', segment: 'gerente', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/historial/historial.module.ngfactory#HistorialPageModuleNgFactory', name: 'HistorialPage', segment: 'historial', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/historial2/historial2.module.ngfactory#Historial2PageModuleNgFactory', name: 'Historial2Page', segment: 'historial2', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/inicio/inicio.module.ngfactory#InicioPageModuleNgFactory', name: 'InicioPage', segment: 'inicio', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mercadeo/mercadeo.module.ngfactory#MercadeoPageModuleNgFactory', name: 'MercadeoPage', segment: 'mercadeo', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/reporteo/reporteo.module.ngfactory#ReporteoPageModuleNgFactory', name: 'ReporteoPage', segment: 'reporteo', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/reports-user/reports-user.module.ngfactory#ReportsUserPageModuleNgFactory', name: 'ReportsUserPage', segment: 'reports-user', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tiendas-gerentes/tiendas-gerentes.module.ngfactory#TiendasGerentesPageModuleNgFactory', name: 'TiendasGerentesPage', segment: 'tiendas-gerentes', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/aspects/aspects.module.ngfactory#AspectsPageModuleNgFactory', name: 'AspectsPage', segment: 'aspects', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Camera,

    { provide: ErrorHandler, useClass: IonicErrorHandler },


  ]
})
export class AppModule { }



